<template>
    <div class="content">
        <div class="intro-y col-span-12 lg:col-span-12">
            <div class="intro-y box">
                <div
                    class="flex flex-col sm:flex-row items-center p-5 border-b border-slate-200/60 dark:border-darkmode-400">
                    <h2 class="font-medium text-base mr-auto">
                        Add User Form
                    </h2>
                </div>
                <div id="vertical-form" class="p-5">
                    <div class="preview">
                        <form v-on:submit.prevent="onSubmit" method="post">
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">First Name</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="First Name"
                                    v-model="form.firstName" name="firstName">
                                <span v-if="errors.firstName" class="text-danger">{{ errors.firstName }}</span>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Last Name</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Last Name"
                                    v-model="form.lastName" name="lastName">
                                <span v-if="errors.lastName" class="text-danger">{{ errors.lastName }}</span>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Email</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="email@gmail.com"
                                    v-model="form.email">
                                <span v-if="msg.email" class="text-danger">{{ msg.email }}</span>
                                <span v-if="emailError" class="text-danger">{{ emailError }}</span>
                                <span v-if="errors.email" class="text-danger">{{ errors.email }}</span>

                            </div>

                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Password</label>
                                <input id="vertical-form-2" type="password" class="form-control" v-model="form.password"
                                    name="password">
                                <span v-if="errors.password" class="text-danger">{{ errors.password }}</span>

                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Date of Year</label>
                                <input id="vertical-form-2" type="text" class="form-control" v-model="form.year"
                                    name="year" />
                                <span v-if="errors.year" class="text-danger">{{ errors.year }}</span>

                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Months of Birth</label>
                                <select v-model="form.months"
                                    class="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                                    <option value="">Select a month</option>
                                    <option v-for="(month, index) in monthName" :key="index" :value="index + 1">{{ month }}
                                    </option>
                                </select>
                                <span v-if="errors.months" class="text-danger">{{ errors.months }}</span>

                            </div>

                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Country</label>
                                <input id="vertical-form-2" type="text" class="form-control" v-model="form.country"
                                    name="country" />
                                <span v-if="errors.country" class="text-danger">{{ errors.country }}</span>

                            </div>
                            <button class="btn btn-primary mt-5" type="submit">Add</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    postRequest
} from '../../pages/ApiFunctions';
import * as Yup from 'yup';
import Swal from 'sweetalert2';

const schema = Yup.object().shape({
    firstName: Yup.string().required('First Name is required'),
    lastName: Yup.string().required('Last Name is required'),
    email: Yup.string().email().required('Email is required'),
    password: Yup.string().min(6, 'Password must be at least 6 characters').required('Password is required'),
    year: Yup.number().required('Year is required'),
    months: Yup.string().required('Months is required'),
    country: Yup.string().required('Country is required'),
});
export default {
    name: 'AddUser',
    data() {
        return {
            monthName: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
            emailError: '',
            msg: [],
            form: {
                firstName: "",
                lastName: "",
                email: "",
                phoneNumber: "",
                password: "",
                year: "",
                months: '',
                country: "",
            },
            errors: {},
        }
    },
    methods: {
        async onSubmit() {
            try {
                await schema.validate(this.form, { abortEarly: false })
                const response = await postRequest('/register', this.form);
                if (response.error) {
                    this.emailError = response.error;
                }
                else if (response.status === 500) {
                    Swal.fire({
                        icon: 'error',
                        title: 'Internal Server Error',
                        text: 'Something went wrong. Please try again later.',
                    });
                }
                else {
                    this.emailError = ''
                    this.$router.push({
                        path: '/admin/user-data-list',
                        name: 'UserDataList',
                    });
                    Swal.fire({
                        title: 'Success!',
                        text: 'Data added successfully',
                        icon: 'success',
                        confirmButtonText: 'OK'
                    });
                }
            }
            catch (error) {
                if (error.name === 'ValidationError') {

                    const formErrors = {};
                    error.inner.forEach((e) => {
                        formErrors[e.path] = e.message;
                    });
                    this.errors = formErrors;
                }
            }
        },
        validateEmail(value) {
            if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value)) {
                this.msg['email'] = '';
            } else {
                this.msg['email'] = 'Invalid Email Address';
            }
        },

    },
    watch: {
        email(value) {
            // binding this to the data value in the email input
            this.post.email = value;
            this.validateEmail(value);
        }
    },

}
</script>



